* {
  box-sizing: border-box;
  margin: 0;
  padding: 0
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4
}

a {
  color: #333;
  text-decoration: none
}

.container {
  
  max-width: 600px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  border: 1px solid rgb(168, 167, 167);
}

.btn {
  display: inline-block;
  border: none;
  background: rgb(136, 135, 135);
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}

.about-div {
  padding: 10px;
}